<template>
    <div class="page-index">
        <div v-if="$route.path == '/my/mycertification'">
            <van-nav-bar            
                left-arrow
                title="我的认证"
                @click-left="onClickLeft"
            >    
            </van-nav-bar>
            <div class="uncertified" v-if="!data || !data.length">
                <img src="../../assets/images/icon_uncertified.png" >
                <p class="text">暂时没有已认证车辆</p>
            </div>
            <ul class="list" v-if="data">
                <li v-for="(item,key) in data" :key="key" @click="handleToVeriftForm('edit',item)">
                    <div class="flex justify-space-between">
                        <div class="left flex">
                            <div class="img-box">
                                <img class="icon" :src="item.icon" />
                                <img class="scarvip" src="../../assets/images/icon_iscarvip.png" v-if="item.carstatus == 1" >
                            </div>

                            <div>
                                <p class="car_1">{{item.car_1}}{{item.car_2}}</p>
                                <p>{{item.car_3}}</p>
                            </div>
                        </div>
                        <div class="right">
                            <p  :class="{'gray':item.status !='已认证'}">{{item.status}}</p>
                        </div>
                      
                    </div>
                    <div class="btn car-btn" :class="{'btn-gray':item.carstatus == 0 }" v-if="item.carstatus !=0 && item.carstatus !=1 " @click='handleCarOwner(item)' >
                        <p>
                            <span v-if="item.carstatus ==2">已拒绝</span>
                            <span v-if="item.carstatus ==3">审核中...</span>
                            <span v-if="item.carstatus ==4">一键申请</span>
                        </p>
                        <p class="text">（只能选择一款认证为车辆代表）</p>
                    </div>
                    <div class="btn" :class="{'btn-gray-x':item.show_plate_model != item.vid }" v-if="item.status == '已认证'" @click="addShowCarModel(item.vid)">显示此车辆</div>
                </li>
            </ul>
            <div class="bottom" v-if="data || data.length < 3">
                <p class="text">温馨提示，每人最多能认证3辆车</p>
                <div class="btn btn-add" @click="handleToVeriftForm('add')">添加车辆</div>
                <div class="btn btn-back" @click="onClickLeft">返回</div>
            </div>            
        </div>


        <router-view :itemdata="itemdata" > </router-view>
    </div>
</template>

<script>
import { Toast,Dialog } from 'vant';
export default {
    data(){
        return {
            data:'',
            itemdata:''
        }   
    },
    created(){
        this.getMyCarVerify();
    },
    methods:{
        onClickLeft(){
            this.$router.go(-1)
        },
        getMyCarVerify(){
            this.api.getMyCarVerify({
                token:this.$store.state.userData,
                uid:0
            }).then(res=>{
                if(res.err_code == 0){
                    
                    this.data = res.data.list
                }
            })
        },
        handleCarOwner(item){
            if(item.carstatus == 4){
                Dialog.confirm({
                    title: '确认一键申请车代表？',   
                }).then(() => {
                    this.addApplyCarOwner(item.vid);
                }).catch(() => {
                });
            }
        },
        // 选择显示认证的车型
        addShowCarModel(vid){
            Dialog.confirm({
                title: '确认选择显示认证该车型？',   
            }).then(() => {
                this.api.addShowCarModel({
                    vid:vid,
                    token:this.$store.state.userData,
                }).then( res => {
                    if(res.err_code == 0){
                        this.getMyCarVerify();
                    }else {
                        Toast(res.err_msg);
                    }
                })
            }).catch(() => {
            });

        },
        // 车代表一键申请
        addApplyCarOwner(vid){
            this.api.addApplyCarOwner({
                vid:vid,
                token:this.$store.state.userData,
            }).then( res => {
                if(res.err_code == 0){
                    this.getMyCarVerify();
                }else {
                    Toast(res.err_msg);
                }
            })
        },
        // 添加，编辑
        handleToVeriftForm(v,item){
            switch(v){
                case 'add':
                    this.$router.push({ path: '/my/mycertification/verifyform',query:{type:v} })
                    break;
                case 'edit':
                    if(item.status == '不通过'){
                        this.itemdata = item;
                        this.$router.push({ path: '/my/mycertification/verifyform',query:{type:v,vid:item.vid} })
                    }
                    break;    
            }
        },
        handleToPage(v){
           
        },  
    },
    beforeRouteUpdate (to, from, next) {
        if(to.path == '/my/mycertification'){
            this.getMyCarVerify();
        }
        next();
    },
}
</script>
